.header {
    background-color: #003366;
    color: #fff;
    padding: 10px 20px;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
    display: flex;
    flex-direction: column; /* Stack logo and nav vertically */
    align-items: center; /* Center align the children */
}

.logo img {
    width: 120px;
    margin-bottom: 10px; /* Space between logo and navigation */
}

.navList {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center; /* Center navigation items */
    width: 100%; /* Full width to allow centering */
}

.navItem {
    margin: 0 15px; /* Uniform margin for spacing */
}

.navLink {
    color: white;
    text-decoration: none;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}
